function comment(){
	var swiper = new Swiper('.section-slide .swiper-container', {
		slidesPerView: 3,
		spaceBetween: 30,
		navigation: {
			nextEl: '.section-slide .swiper-button-next',
			prevEl: '.section-slide .swiper-button-prev',
			type: 'bullets',
			clickable: true
		},
		breakpoints: {
			1024:{
				slidesPerView: 3,
				spaceBetween: 10,
			},
			768: {
				slidesPerView: 2,
				spaceBetween: 10,
			},
			575: {
				slidesPerView: 1,
				spaceBetween: 0,
			}
		},
	});
}
document.addEventListener('DOMContentLoaded', () => {
	comment();
});